.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.follow-mountain {
  float: left;
  shape-outside: polygon(0 100px, 0 600px, 450px 600px);
  width: 50vw;
  height: 600px;
  transition: shape-outside 1s linear;
  z-index: 5;
}

.follow-mountain:hover {
  /* shape-outside: polygon(0 100px, 0 600px, 400px 600px); */
}

span.highlight {
  color: var(--dark-rock);
  transition: color 1s;
}

span.highlight:hover {
  color: var(--red-rock);
}

.portfolio-item {
  position: absolute;
  left: 0;
  top: 0;
  transition: all 500ms ease-out;
}

#portfolio-wrapper .columns { margin-bottom: 36px; }

#portfolio-wrapper {
  width: 100%;
  height: 600px;
}

/* scrolldown link */
.scrolldown {
  position: relative;
  bottom: 50px;
  z-index: 3;
  }

.scrolldown a i{
  color: #fff;
}

#resume .scrolldown a i{
  color: var(--red-rock);
}

#resume .scrolldown a i:hover { color: var(--star); }

.scrolldown a {
  margin: 0 auto;

   display: block;
   height: 42px;
   width: 42px;
   font-size: 42px;
   line-height: 42px;
   border-radius: 100%;

   -webkit-transition: all .3s ease-in-out;
   -moz-transition: all .3s ease-in-out;
   -o-transition: all .3s ease-in-out;
   transition: all .3s ease-in-out;
}

#about .main-col p {
 transition: opacity .5s linear;
}

#about p.text-right {
  pointer-events: none;
  margin-bottom: 3px;
}

#about .contact-details p{
  mix-blend-mode: difference;
  background-color: rgba(255,255,255,.6);
  transition: all .5s ease-in;
}

#about .contact-details p:hover{
  background-color: rgba(255,255,255,1);

}


.hidden {
  opacity: 0;
}

.trigger {
  position: absolute;
}

.trigger .hide {
  opacity: 0;
}


.scrolldown a i:hover { color: var(--star); }
